import request from '@/utils/request'
// 
export const getUser = params => {
  return request({
    method: 'GET',
    url: '/user/detail',
    // dataPOST
    params
  })
}
// 
export const getSongSheet = params => {
  return request({
    method: 'GET',
    url: '/user/playlist',
    // dataPOST
    params
  })
}
